import { useCallback, useEffect, useState } from 'react'
import BigNumber from 'bignumber.js'
import { useWeb3React } from '@web3-react/core';
import { SHORT_TWAP_MIN, SHORT_TWAP_MAX } from '../utils/utils';
import { getOracle } from '../utils/contracts';
import useBlock from './useBlock'
import { useBnbPriceState } from '../state/bnbPriceContext'

const useAveragePrice = (tokenAddress) => {
    const { account, library, chainId } = useWeb3React()
    const [price, setPrice] = useState('0')
    const block = useBlock()
    const bnbUsdPrice = useBnbPriceState()

    const fetchPrice = useCallback(async (_tokenAddress) => {
        const contract = getOracle(library, chainId);
        const _price = await contract.methods.computeAverageTokenPrice(
            _tokenAddress,
            SHORT_TWAP_MIN,
            SHORT_TWAP_MAX
        ).call();
        const usePrice = new BigNumber(_price).div(2 ** 112).times(bnbUsdPrice)
        setPrice(usePrice);
    }, [bnbUsdPrice, chainId, library])

    useEffect(() => {
        if (account && library && tokenAddress) {
            fetchPrice(tokenAddress)
        }
    }, [account, library, tokenAddress, chainId, block, fetchPrice])

    return price
}

export default useAveragePrice
