import { useCallback, useEffect, useState } from 'react'
import BigNumber from 'bignumber.js'
import { useWeb3React } from '@web3-react/core';
import { getPool } from '../utils/contracts';
import { makeContract } from '../utils/utils';
import { abis } from '@project/contracts';

const usePoolTokenBalances = (poolAddress, poolTokens) => {
    const { account, library, chainId } = useWeb3React()
    const [balance, setBalance] = useState(['0'])
    const [updateBalance, setUpdateBalance] = useState(false);

    const fetchBalances = useCallback(async (tokenArr) => {
        const contract = makeContract(library, abis.indexpool, poolAddress);
        const bals = [];
        for (let i = 0; i < tokenArr.length; i++) {
            const _balance = await contract.methods.getBalance(tokenArr[i]).call();
            bals.push(_balance);

        }
        setBalance(bals);
        setUpdateBalance(true);
    }, [library, poolAddress])

    useEffect(() => {
        if (account && library) {
            fetchBalances(poolTokens)

        }

    }, [account, library, poolTokens, chainId, updateBalance, fetchBalances])

    return balance
}

export default usePoolTokenBalances
