import React, { useCallback } from 'react'
import {
  HashRouter as Router,
  Switch,
  Route,
  useParams
} from 'react-router-dom'

import {
  makeStyles,
  ThemeProvider
} from '@material-ui/core/styles'

import {
  Grid,
} from '@material-ui/core'

import { theme } from './theme'

import useWeb3Modal from './hooks/useWeb3Modal'
import Header from './components/header/Header.component'
import Footer from './components/footer/Footer.component'

import Funds from './containers/funds/Funds.component'
import Dashboard from './containers/dashboard/Dashboard.component'

import { ToastProvider } from 'react-toast-notifications'

import Web3 from 'web3'

import { BnbPriceContextProvider } from './state/bnbPriceContext'

function App() {
  const [darkMode, setDarkmode] = React.useState(true)
  const [provider, loadWeb3Modal, logoutOfWeb3Modal] = useWeb3Modal()

  const checkConnection = useCallback(async () => {
    // Check if browser is running Metamask
    let web3
    if (window.ethereum) {
      web3 = new Web3(window.ethereum);
    } else if (window.web3) {
      web3 = new Web3(window.web3.currentProvider);
    };

    // Check if User is already connected by retrieving the accounts
    web3.eth.getAccounts()
      .then(async (accounts) => {
        if (accounts.length > 0) {
          if (!provider) {
            loadWeb3Modal()
          } else {
            logoutOfWeb3Modal()
          }
        }
      });
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])

  React.useEffect(() => {
    checkConnection();
  }, [checkConnection]);

  const useStyles = makeStyles((_theme) => ({
    container: {
      minHeight: '100vh',
      backgroundColor: darkMode ? theme.palette.background.dark : theme.palette.background.light,
    },
    header: {
      zIndex: 0
    },
    footer: {
      zIndex: 0
    }
  })
  )
  const classes = useStyles()

  function Child() {
    // We can use the `useParams` hook here to access
    // the dynamic pieces of the URL.
    let { address } = useParams()

    return (
      <Funds poolAddress={address} />
    )
  }


  return (
    <ThemeProvider theme={theme}>
      <ToastProvider
        autoDismiss
        autoDismissTimeout={6000}
        placement="bottom-center"
      >
        <BnbPriceContextProvider>
          <Router>
            <Grid
              container
              className={classes.container}
            >
              <Grid item xs={12} className={classes.header}>
                <Header
                  title="Project"
                  nav1="nav1"
                  nav2="nav2"
                  nav3="nav3"
                  nav4="about"
                  provider={provider}
                  loadWeb3Modal={loadWeb3Modal}
                  logoutOfWeb3Modal={logoutOfWeb3Modal}
                />
              </Grid>
              <Grid item xs={12}>
                <Switch>
                  <Route path="/" exact component={Dashboard} />
                  <Route path="/Dashboard" component={Dashboard} />
                  <Route path="/Funds/:address" children={<Child />} />
                </Switch>
              </Grid>
              <Grid item xs={12} className={classes.footer}>
                <Footer
                  nav1="nav1"
                  nav2="nav2"
                  nav3="nav3"
                />
              </Grid>
            </Grid>
          </Router>
        </BnbPriceContextProvider>
      </ToastProvider>
    </ThemeProvider>
  )
}

export default App

