import React from 'react';
import { useWeb3React } from '@web3-react/core';
import { abis } from "@project/contracts";
import { useToasts } from 'react-toast-notifications'
import { makeContract } from '../utils/utils';

const useJoinSwapPool = (poolAddress, tokenIn, tokenAmountIn, minPoolAmountOut) => {
    const { account, library, chainId } = useWeb3React()
    const { addToast } = useToasts()

    const joinswap = async (

    ) => {
      addToast('Waiting for transaction succes...', {
          appearance: 'info',
          autoDismiss: true,
      })
      const contract = makeContract(library, abis.indexpool, poolAddress);
    debugger
      await contract.methods.joinswapExternAmountIn(
        tokenIn,
        tokenAmountIn,
        minPoolAmountOut
      ).send({from: account}).then(()=> {
          addToast('Transaction Succes!', {
              appearance: 'success',
              autoDismiss: true,
          })

      }).catch((err) => {
        if(err.message.includes("User denied transaction signature")) {
          addToast('Denied Transaction', {
              appearance: 'error',
              autoDismiss: true,
          })
        } else {
          addToast('Transaction Failed', {
              appearance: 'error',
              autoDismiss: true,
          })
        }
      })

    }

    const handleJoinSwap = React.useCallback(
      async () => {
        await joinswap()
      },
      [account, poolAddress, tokenIn, tokenAmountIn, minPoolAmountOut],
    )

    return { onJoinSwapPool: handleJoinSwap }
  }

  export default useJoinSwapPool
