import React from "react";
import { Grid, Typography, Button } from "@material-ui/core";
import BigNumber from "bignumber.js";
import { formatter, getCoinAvatar } from "../../../utils/utils";
import {
  calcAllOutGivenPoolIn,
  calcSingleOutGivenPoolIn,
  calcSingleInGivenPoolOut
} from "../../../utils/bmath";
import { useWeb3React } from "@web3-react/core";
import { useStyles } from "./BurnPanel.styles";

import InputToken from "../../inputs/InputToken";
import TokenList from "../../lists/tokenlist/TokenList.component";

import useApprove from "../../../hooks/useApprove";
import useExitPool from "../../../hooks/useExitPool";
import useExitSwapPool from "../../../hooks/useExitSwapPool";
import useAllowance from "../../../hooks/useAllowance";
import useTokenBalance from '../../../hooks/useTokenBalance';

import usePoolTokenInfo from "../../../hooks/usePoolTokenInfo";
import { BurnWithBnbButton } from "../../buttons/MintWithBnbButton";
import useSwapBnbForAllTokensBurn from "../../../hooks/useSwapBnbForAllTokensBurn";
import { addresses } from "@project/contracts";

const BurnPanel = ({ poolInfo }) => {
  const { account, library, chainId } = useWeb3React();
  const [
    potentialBnbSwappedAmount,
    setPotentialBnbSwappedAmount
  ] = React.useState(new BigNumber(0));
  const [inputValue, setInputValue] = React.useState("0");
  const [amounts, setAmounts] = React.useState(["0"]);
  const poolAllowance = useAllowance(poolInfo.address, poolInfo.address);
  const [inputError, setInputError] = React.useState();
  const [selected, setSelected] = React.useState();

  const selectedTokenInfo = usePoolTokenInfo(poolInfo.address, selected);
  const tokenBalance = useTokenBalance(poolInfo.address)

  const [singleAmount, setSingleAmount] = React.useState();
  const bnbTokenInfo = usePoolTokenInfo(
    poolInfo.address,
    addresses[chainId].WBNB
  );
  const { onExitPool } = useExitPool(poolInfo.address, inputValue, amounts);
  const { onExitSwapPool } = useExitSwapPool(
    poolInfo.address,
    selected,
    inputValue,
    singleAmount
  );

  const { onSwapBnbForTokensAndBurn } = useSwapBnbForAllTokensBurn(
    poolInfo.address,
    amounts,
    poolInfo.tokens.length,
    inputValue,
    potentialBnbSwappedAmount
  );

  const { onApprove } = useApprove(
    poolInfo.address,
    inputValue,
    poolInfo.address
  );

  const classes = useStyles();

  React.useEffect(() => {
    if (bnbTokenInfo.balance !== undefined) {
      const _amountSingle = calcSingleInGivenPoolOut(
        new BigNumber(bnbTokenInfo.balance),
        new BigNumber(bnbTokenInfo.weight),
        new BigNumber(poolInfo.totalSupply),
        new BigNumber(poolInfo.totalWeight),
        new BigNumber(inputValue),
        new BigNumber(poolInfo.swapFee)
      );
      setPotentialBnbSwappedAmount(_amountSingle);
    }
  }, [
    bnbTokenInfo.balance,
    bnbTokenInfo.weight,
    inputValue,
    poolInfo.swapFee,
    poolInfo.totalSupply,
    poolInfo.totalWeight
  ]);

  const handleAmounts = React.useCallback(
    (inputAmount, _poolInfo) => {
      if (selected == null && _poolInfo !== undefined) {
        const _amounts = calcAllOutGivenPoolIn(
          poolInfo.balances,
          poolInfo.weights,
          poolInfo.totalSupply,
          inputAmount
        );
        setAmounts(_amounts);
        setSingleAmount(undefined);
      } else if (selected !== null && selectedTokenInfo.balance !== undefined) {
        const _amountSingle = calcSingleOutGivenPoolIn(
          new BigNumber(selectedTokenInfo.balance),
          new BigNumber(selectedTokenInfo.weight),
          new BigNumber(_poolInfo.totalSupply),
          new BigNumber(_poolInfo.totalWeight),
          new BigNumber(inputAmount),
          new BigNumber(_poolInfo.swapFee)
        );

        const _amounts = calcAllOutGivenPoolIn(
          _poolInfo.balances,
          poolInfo.weights,
          poolInfo.totalSupply,
          "0"
        );
        setAmounts(_amounts);
        setSingleAmount(_amountSingle.toFixed());
      }
    },
    [
      poolInfo.balances,
      poolInfo.totalSupply,
      poolInfo.weights,
      selected,
      selectedTokenInfo.balance,
      selectedTokenInfo.weight
    ]
  );

  React.useEffect(() => {
    handleAmounts(inputValue, poolInfo);
  }, [handleAmounts, inputValue, poolInfo]);

  const handleChange = value => {
    setInputError();
    if (Number(value) > 0 && poolInfo) {
      const inputValue = library.utils.toWei(value, "ether");
      if (new BigNumber(inputValue).gt(poolInfo.totalSupply)) {
        setInputError(
          `Max value is ${new BigNumber(poolInfo.totalSupply)
            .div(1e18)
            .toFixed(4)} ${poolInfo.symbol}`
        );
        return;
      }
      setInputValue(inputValue);
      const _amounts = calcAllOutGivenPoolIn(
        poolInfo.balances,
        poolInfo.weights,
        poolInfo.totalSupply,
        inputValue
      );
      // console.log(_amounts);
      setAmounts(_amounts);
    } else {
      const inputValue = "0";
      setInputValue(inputValue);
      const _amounts = calcAllOutGivenPoolIn(
        poolInfo.balances,
        poolInfo.weights,
        poolInfo.totalSupply,
        inputValue
      );
      setAmounts(_amounts);
      setPotentialBnbSwappedAmount(new BigNumber(0));
    }
    handleAmounts(inputValue, poolInfo);
  };

  const handleSelect = single => {
    setSelected(single);
  };

  return (
    <Grid
      container
      spacing={0}
      alignItems="baseline"
      justify="center"
      className={classes.root}
    >
      <Grid item xs={6}>
        <InputToken
          onChange={handleChange}
          info={getCoinAvatar(poolInfo.address, chainId)}
          size="medium"
          error={inputError}
          placeholder='0'
          maxButton
        />
      </Grid>
      <Grid item xs={3} textAlign="center">
        <div className={classes.buttonBox}>
          {poolAllowance >= inputValue ? (
            <Button
              variant="contained"
              color="primary"
              size="large"
              onClick={selected == null ? onExitPool : onExitSwapPool}
              disabled={selected || inputError || inputValue <= 0 || inputValue > tokenBalance}
              className={classes.button}
            >
              BURN
            </Button>
          ) : (
            <Button
              variant="contained"
              color="primary"
              size="large"
              onClick={onApprove}
              className={classes.button}
            >
              APPROVE
            </Button>
          )}
        </div>
      </Grid>
      {/* <Grid item xs={3}>
        <BurnWithBnbButton
          variant="contained"
          color="primary"
          size="small"
          onClick={onSwapBnbForTokensAndBurn}
          disabled
          // disabled={selected || inputError || inputValue <= 0}
          bnbToSendValue={potentialBnbSwappedAmount}
        />
      </Grid> */}
      <Grid item xs={12}>
        <TokenList
          poolAddress={poolInfo.address}
          poolAmounts={amounts}
          poolTokens={poolInfo.tokens}
          withButton={false}
          handleSelect={handleSelect}
          singleAmount={singleAmount}
        />
      </Grid>
    </Grid>
  );
};

export default BurnPanel;
