import React from "react";
import {
  Grid,
  Typography,
  Button
} from '@material-ui/core'
import { useParams, useHistory } from "react-router-dom";
import { useStyles } from './Funds.styles'
import { useWeb3React } from "@web3-react/core";
import { addresses, abis } from "@project/contracts";
import InfoCard from '../../components/cards/infocard/InfoCard.component'
import ActionCard from '../../components/cards/actioncard/ActionCard.component'
import useCurrentTokens from '../../hooks/useCurrentTokens';

import usePoolInfo from "../../hooks/usePoolInfo";

function Funds({ poolAddress }) {
  const { account, chainId } = useWeb3React();
  const classes = useStyles();
  const poolInfo = usePoolInfo(poolAddress);

  let history = useHistory();

  React.useEffect(() => {

    if (!poolInfo && chainId != 56) {
      history.push("/dashboard");
    }
    if (poolInfo) {

    }

    return () => {

    }
  }, [account, chainId, poolInfo, poolAddress, history]);

  return (
    <Grid
      container
      spacing={4}
      alignItems='flex-start'
      justify='center'
      className={classes.container}
    >
      {poolInfo && chainId == 56 ?
        <>
          <Grid item lg={8} className={classes.subcontainer}>
            <InfoCard poolInfo={poolInfo} />
          </Grid>
          <Grid item lg className={classes.subcontainer}>
            <ActionCard poolInfo={poolInfo} />
          </Grid>
        </>
        :
        <></>
      }
    </Grid>
  );
}

export default Funds;

