import React, { createContext, useContext, useState, useEffect } from 'react';

const BnbPriceContext = createContext(undefined);
const updatedIntervalSec = 10 * 1000;

export function BnbPriceContextProvider({ children }) {
  const [price, setPrice] = useState(0)

  useEffect(() => {
    const updatePrice = () =>
      fetch('https://api.coingecko.com/api/v3/simple/price?ids=binancecoin&vs_currencies=usd')
        .then((res) => res.json())
        .then((res) => {
          const { binancecoin: { usd: price } } = res;
          setPrice(price)
        })

    updatePrice()
    try {

      setInterval(() => {
        updatePrice()
      }, updatedIntervalSec);
    } catch (error) {

    }
  }, []);

  return <BnbPriceContext.Provider value={price}>
    {children}
  </BnbPriceContext.Provider>
}

export const useBnbPriceState = () => {
  return useContext(BnbPriceContext);
};
