export const MAX_UINT = '0xffffffffffffffffffffffffffffffffffffffffffffffffffffffffffffffff'
export const MAX_UINT_96 = '0xffffffffffffffffffffffff'

export const formatter = new Intl.NumberFormat('en-US',
    // These options are needed to round to whole numbers if that's what you want.
    //minimumFractionDigits: 0, // (this suffices for whole numbers, but will print 2500.10 as $2,500.1)
    //maximumFractionDigits: 0, // (causes 2500.99 to be printed as $2,501)
);


export const SHORT_TWAP_MIN = '60';
export const SHORT_TWAP_MAX = '116000';

export const makeContract = (library, abi, address) => {
    return new library.eth.Contract(abi, address);
}
export const isApproved = (allowance) => Number(allowance) >= Number(MAX_UINT_96)


const blocksPerYear = new BigNumber(10518975);

export const getTokenAvatar = (coinaddress, chainId) => {

    if (coinaddress === addresses[chainId].WBNB) {

        return { img: wbnb, tokenname: 'WRAPPED BNB', symbol: 'WBNB', address: coinaddress };

    } else if (coinaddress === addresses[chainId].CAKE) {

        return { img: cake, tokenname: 'CAKE', symbol: 'CAKE', address: coinaddress };

    } else if (coinaddress === addresses[chainId].SWIPE) {

        return { img: swipe, tokenname: 'SWIPE', symbol: 'SWIPE', address: coinaddress };

    } else if (coinaddress === addresses[chainId].BAKE) {

        return { img: bakerytoken, tokenname: 'BAKE', symbol: 'BAKE', address: coinaddress };

    } else if (coinaddress === addresses[chainId].VENUS) {

        return { img: venus, tokenname: 'VENUS', symbol: 'VENUS', address: coinaddress };

    } else if (coinaddress === addresses[chainId].ELLIPSIS) {

        return { img: ellipsis, tokenname: 'ELLIPSIS', symbol: 'ELLIPSIS', address: coinaddress };

    } else if (coinaddress === addresses[chainId].BANANA) {

        return { img: banana, tokenname: 'BANANA', symbol: 'BANANA', address: coinaddress };

    } else if (coinaddress === addresses[chainId].SAFEPAL) {

        return { img: safepal, tokenname: 'SAFEPAL', symbol: 'SAFEPAL', address: coinaddress };

    } else if (coinaddress === addresses[chainId].TWT) {

        return { img: trust, tokenname: 'TRUST WALLET', symbol: 'TWT', address: coinaddress };

    } else if (coinaddress === addresses[chainId].BIFI) {

        return { img: bifi, tokenname: 'BEEFY FINANCE', symbol: 'BIFI', address: coinaddress };

    } else if (coinaddress === addresses[chainId].AUTO) {
        return { img: autofarm, tokenname: 'AUTOFARM', symbol: 'AUTO', address: coinaddress };

    } else if (coinaddress === addresses[chainId].BSCPAD) {

        return { img: bscpad, tokenname: 'BSC PAD', symbol: 'BSCPAD', address: coinaddress };

    } else if (coinaddress === addresses[chainId].BUNNY) {

        return { img: bunny, tokenname: 'BUNNY', symbol: 'BUNNY', address: coinaddress };

    } else if (coinaddress === addresses[chainId].BISON) {

        return { img: bison, tokenname: 'BISON', symbol: 'BISON', address: coinaddress };

    } else if (coinaddress === addresses[chainId].biT10) {

        return { img: biT10, tokenname: 'BSC Top 10', symbol: 'biT10', address: coinaddress };

    } else if (coinaddress === addresses[chainId].mockPool) {

        return { img: biT10, tokenname: 'Mock Pool', symbol: 'moT9', address: coinaddress };
    }

    return { img: '', tokenname: '', symbol: '', address: coinaddress };;
}

