import React from 'react'
import { Grid, AppBar, Toolbar, Typography, Modal, Link as MaterialLink} from '@material-ui/core'
import { Link } from 'react-router-dom'
import { useStyles } from './Footer.styles'
import { useWeb3React } from '@web3-react/core';
import useBlock from '../../hooks/useBlock';

import TwitterIcon from '@material-ui/icons/Twitter';
import GitHubIcon from '@material-ui/icons/GitHub';
import TelegramIcon from '@material-ui/icons/Telegram';
import { DiscordIcon } from '../icons';
import { AiFillMediumSquare } from 'react-icons/ai';


const Footer = ({title, nav1, nav2, nav3}) => {
    const { account } = useWeb3React();
    const classes = useStyles();
    const block = useBlock();
    
    React.useEffect(() => {
       
    }, [account, block])

    return (
        <AppBar position='relative' color='transparent' elevation={1} >
            <Toolbar className={classes.footer}>
                <Grid 
                    container
                    justify='space-between'
                    spacing={2}
                >
                
                <Grid container item direction='column' xs={3}>
                    <Grid 
                        container
                        item
                        spacing={2}
                        alignItems="center"
                        justify="flex-start"
                    >
                        <Grid item >
                            <Typography 
                                className={classes.title}  
                                component={MaterialLink}
                                color="textPrimary"
                                variant="subtitle1" 
                                target="_blank" 
                                href='https://bishares.finance/assets/docs/bishares-gordian-audit.pdf'
                                noWrap
                            >
                                Gordian Audit 
                            </Typography>
                        </Grid>
                        <Grid item >
                            <Typography 
                                className={classes.title}  
                                color="textPrimary"
                                variant="subtitle1" 
                                noWrap
                            >
                                |
                            </Typography>
                        </Grid>
                        <Grid item >
                            <Typography 
                                className={classes.title}  
                                component={MaterialLink}
                                color="textPrimary"
                                variant="subtitle1" 
                                target="_blank" 
                                href='https://bishares.finance/assets/docs/bishares-defiyield-audit.pdf'
                                noWrap
                            >
                                DefiYield Audit
                            </Typography>
                        </Grid>
                    </Grid>

                    <Grid 
                        container
                        item
                        spacing={2}
                        alignItems="center"
                        justify="flex-start"
                    >
                        <Grid item  >
                            <Typography 
                                className={classes.title} 
                                component={MaterialLink}
                                style={{textDecoration: 'none'}}
                                href='https://twitter.com/BiSharesFinance'
                                target="_blank" 
                                color="textPrimary"
                                variant="h5" 
                                noWrap
                            >
                                <TwitterIcon />
                            </Typography>
                        </Grid> 
                        <Grid item  >
                            <Typography 
                                className={classes.title} 
                                component={MaterialLink}
                                style={{textDecoration: 'none'}}
                                href='https://github.com/bisharesindexfund'
                                target="_blank" 
                                color="textPrimary"
                                variant="h5" 
                                noWrap
                            >
                                <GitHubIcon />
                            </Typography>
                        </Grid> 
                        <Grid item  >
                            <Typography 
                                className={classes.title} 
                                component={MaterialLink}
                                style={{textDecoration: 'none'}}
                                href='https://t.me/bishares'
                                target="_blank" 
                                color="textPrimary"
                                variant="h5" 
                                noWrap
                            >
                                <TelegramIcon />
                            </Typography>
                        </Grid>
                        <Grid item  >
                            <Typography 
                                className={classes.title} 
                                component={MaterialLink}
                                style={{textDecoration: 'none'}}
                                href='https://bi-shares.medium.com'
                                target="_blank" 
                                color="textPrimary"
                                variant="h5" 
                                noWrap
                            >
                                <AiFillMediumSquare style={{ fontSize: '24px' }} />
                            </Typography>
                        </Grid>  
                        <Grid item  >
                            <Typography 
                                className={classes.title} 
                                component={MaterialLink}
                                style={{textDecoration: 'none'}}
                                href='https://discord.com/invite/w2jejq5rBP'
                                target="_blank" 
                                color="textPrimary"
                                variant="h5" 
                                noWrap
                            >
                                <DiscordIcon />
                            </Typography>
                        </Grid> 
                    </Grid>


                </Grid>
                    
                <Grid 
                    container item xs={3}
                    spacing={2}
                    alignItems="center"
                    justify="center"
                >
                    <Grid item >
                        {account ?
                            <Typography 
                                className={classes.title} 
                                color="textPrimary"
                                variant="caption" 
                                noWrap
                            >
                                 Block: {block == 0 ? 'Loading....' : block}
                            </Typography>
                        :
                            <Typography 
                                className={classes.title} 
                                color="textPrimary"
                                variant="caption" 
                                noWrap
                            >
                                 Block: ?
                            </Typography>
                        } 
                    </Grid>
                    <Grid item >
                        <Typography 
                            className={classes.title}  
                            color="textPrimary"
                            variant="caption" 
                            noWrap
                        >
                             2021 by Bishares Finance
                        </Typography>
                    </Grid>
                    
                </Grid>
             
                </Grid>
            </Toolbar>
        </AppBar>  
    )
};

export default Footer;