import React from 'react';
import {
    Grid,
    Typography
} from '@material-ui/core'

import BigNumber from 'bignumber.js'

import Avatar from '@material-ui/core/Avatar';
import ProgressBar from '../progressbar/ProgressBar.component';

import { formatter } from '../../utils/utils'
import { useWeb3React } from '@web3-react/core';
import { useStyles } from './TokenInfo.styles';

import usePoolTokenInfo from '../../hooks/usePoolTokenInfo';

const TokenInfo = ({ poolInfo, poolToken, handleSetUsdValue }) => {
    const { library } = useWeb3React();
    const [weightPercentage, setWeightPercentage] = React.useState(0);
    const [usdValue, setUsdValue] = React.useState(0);

    const poolTokenInfo = usePoolTokenInfo(poolInfo.address, poolToken);

    const classes = useStyles();

    React.useEffect(() => {
        if (poolTokenInfo && poolInfo && poolInfo.totalWeight) {
            const percentage = ((100 * Number(poolTokenInfo.weight)) / Number(poolInfo.totalWeight));
            setWeightPercentage(percentage);
        }

    }, [poolInfo, poolToken, poolTokenInfo])

    React.useEffect(() => {
        if (poolTokenInfo && poolTokenInfo.averagePrice) {
            const tokenUsdValue = new BigNumber(poolTokenInfo.averagePrice).times(poolTokenInfo.balance).div(1e18).toNumber()
            setUsdValue(tokenUsdValue);
        }

    }, [poolInfo, poolToken, poolTokenInfo])

    React.useEffect(() => {
        handleSetUsdValue(poolToken, usdValue)
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [usdValue])

    return (
        <Grid
            container
            spacing={1}
            className={classes.root}
            direction='row'
        >
            { poolTokenInfo ?
                <>
                    <Grid item>
                        <Avatar src={poolTokenInfo.img} className={classes.avatar} />
                    </Grid>
                    <Grid container item xs>
                        <Grid
                            container
                            item
                            xs={12}
                            alignItems="flex-start"
                            spacing={0}
                        >
                            <Grid item lg={3}>
                                <Typography variant="caption" color='textPrimary'>
                                    [{poolTokenInfo.symbol}]
                            </Typography>
                            </Grid>
                            <Grid item lg={3}>
                                <Typography variant="caption" color='textPrimary' noWrap>
                                    {poolTokenInfo.name}
                                </Typography>
                            </Grid>
                            <Grid container item lg={12} spacing={0} justify='flex-end'>
                                <Grid item >
                                    <Typography variant="caption" color='primary' noWrap>
                                        {poolTokenInfo.balance ? formatter.format(Number(library.utils.fromWei(poolTokenInfo.balance, 'ether'))) : ''}
                                    </Typography>
                                </Grid>
                                <Grid item >
                                    <Typography variant="caption" color='primary' noWrap>
                                        {poolTokenInfo.symbol}= {' '}
                                    </Typography>
                                </Grid>
                                <Grid item >
                                    <Typography variant="caption" color='primary' noWrap>
                                        {' '}${formatter.format(usdValue)}
                                    </Typography>
                                </Grid>
                            </Grid>

                        </Grid>
                        <Grid item xs={12}>
                            <ProgressBar percentage={weightPercentage ? weightPercentage : '0'} />
                        </Grid>
                    </Grid>
                </>
                :
                <>
                </>
            }
        </Grid>
    )
}

TokenInfo.defaultProps = {
    handleSetUsdValue: () => { }
}

export default TokenInfo;

