import React from 'react';
import { useWeb3React } from '@web3-react/core';
import { addresses, abis } from "@project/contracts";
import { getPool } from '../utils/contracts'
import { useToasts } from 'react-toast-notifications'
import { makeContract } from '../utils/utils';

const useExitSwapPool = (poolAddress, tokenOut, tokenAmountIn, minAmountOut) => {
    const { account, library, chainId } = useWeb3React()
    const { addToast } = useToasts()

    const exitswap = async (

    ) => {
      addToast('Waiting for transaction succes...', {
          appearance: 'info',
          autoDismiss: true,
      })
      const contract = makeContract(library, abis.indexpool, poolAddress);
      await contract.methods.exitswapPoolAmountIn(
        tokenOut,
        tokenAmountIn,
        minAmountOut
      ).send({from: account}).then(()=> {
          addToast('Transaction Success!', {
              appearance: 'success',
              autoDismiss: true,
          })

      }).catch((err) => {
        if(err.message.includes("User denied transaction signature")) {
          addToast('Denied Transaction', {
              appearance: 'error',
              autoDismiss: true,
          })
        } else {
          addToast('Transaction Failed', {
              appearance: 'error',
              autoDismiss: true,
          })
        }
      })

    }

    const handleExitSwap = React.useCallback(
      async () => {
        await exitswap()
      },
      [account, poolAddress, tokenOut, tokenAmountIn, minAmountOut],
    )

    return { onExitSwapPool: handleExitSwap }
  }

  export default useExitSwapPool
