import React, { useCallback } from "react";
import { Grid, Typography, Button } from "@material-ui/core";
import BigNumber from "bignumber.js";
import { formatter, getCoinAvatar } from "../../../utils/utils";
import {
    calcAllInGivenPoolOut,
    calcSingleInGivenPoolOut
} from "../../../utils/bmath";
import { useWeb3React } from "@web3-react/core";
import { useStyles } from "./MintPanel.styles";
import { addresses } from "@project/contracts";

import InputToken from "../../inputs/InputToken";
import TokenList from "../../lists/tokenlist/TokenList.component";
import { MintWithBnbButton } from "../../buttons/MintWithBnbButton";

import useJoinPool from "../../../hooks/useJoinPool";
import useJoinSwapPool from "../../../hooks/useJoinSwapPool";

import usePoolTokenInfo from "../../../hooks/usePoolTokenInfo";
import useSwapBnbForAllTokensMint from "../../../hooks/useSwapBnbForAllTokensMint";
import useCheckIfAllApproved from '../../../hooks/useCheckIfAllApproved';
import useAllBalanceAvailable from '../../../hooks/useAllBalanceAvailable';
import useBalance from '../../../hooks/useBalance';


const MintPanel = ({ poolInfo }) => {
    const { account, library, chainId } = useWeb3React();
    const classes = useStyles();

    const [inputValue, setInputValue] = React.useState("0");
    const [amounts, setAmounts] = React.useState(["0"]);
    const [singleAmount, setSingleAmount] = React.useState();
    const [inputError, setInputError] = React.useState();
    const [selected, setSelected] = React.useState();
    const [demandBalanceMap, setDemandBalanceMap] = React.useState({});
    const [isMintDisabled, setIsMintDisabled] = React.useState(true);
    const [isDemandGreaterThanBalance, setIsDemandGreaterThanBalance] = React.useState(true);

    const checkIfAllApproved = useCheckIfAllApproved(poolInfo);

    const hasBalanceAvailable = useAllBalanceAvailable(poolInfo, amounts, inputValue);
    const accountBalance = useBalance();

    // hotfix
    const [
        potentialBnbSwappedAmount,
        setPotentialBnbSwappedAmount
    ] = React.useState(new BigNumber(0));

    const selectedTokenInfo = usePoolTokenInfo(poolInfo.address, selected);
    const bnbTokenInfo = usePoolTokenInfo(
        poolInfo.address,
        addresses[chainId].WBNB
    );

    const { onJoinPool } = useJoinPool(poolInfo.address, inputValue, amounts);
    const { onJoinSwapPool } = useJoinSwapPool(
        poolInfo.address,
        selected,
        singleAmount,
        "1"
    );
    const { onSwapBnbForTokensAndMint } = useSwapBnbForAllTokensMint(
        poolInfo.address,
        poolInfo.tokens.length,
        inputValue,
        potentialBnbSwappedAmount
    );

    const handleAmounts = React.useCallback(
        (inputAmount, _poolInfo) => {
            if (selected === null && _poolInfo !== undefined) {
                const _amounts = calcAllInGivenPoolOut(
                    _poolInfo.balances,
                    _poolInfo.totalSupply,
                    inputAmount
                );
                setAmounts(_amounts);
                setSingleAmount(undefined);
            } else if (selected !== null && selectedTokenInfo.balance !== undefined) {
                const _amountSingle = calcSingleInGivenPoolOut(
                    new BigNumber(selectedTokenInfo.balance),
                    new BigNumber(selectedTokenInfo.weight),
                    new BigNumber(_poolInfo.totalSupply),
                    new BigNumber(_poolInfo.totalWeight),
                    new BigNumber(inputAmount),
                    new BigNumber(_poolInfo.swapFee)
                );
                const _amounts = calcAllInGivenPoolOut(
                    _poolInfo.balances,
                    _poolInfo.totalSupply,
                    "0"
                );
                setAmounts(_amounts);
                setSingleAmount(_amountSingle.toFixed());
            }
        },
        [selected, selectedTokenInfo.balance, selectedTokenInfo.weight]
    );

    const demandGreaterThanBalanceHandler = React.useCallback(
        (tokenSymbol, value) => {
            setDemandBalanceMap((prevDemandBalanceMap) => ({ ...prevDemandBalanceMap, [tokenSymbol]: value }))
        }, []
    );

    React.useEffect(() => {
        const values = Object.values(demandBalanceMap);
        if (values.length === poolInfo.tokens.length) {
            const isAnyDemandGreaterThanBalance = values.find((val) => val)
            if (isAnyDemandGreaterThanBalance) {
                setIsDemandGreaterThanBalance(isAnyDemandGreaterThanBalance);
            }
        }
    }, [demandBalanceMap, poolInfo.tokens.length]);


    React.useEffect(() => {
        handleAmounts(inputValue, poolInfo);
        setIsMintDisabled(!checkIfAllApproved || !hasBalanceAvailable);
    }, [handleAmounts, inputValue, poolInfo, checkIfAllApproved, hasBalanceAvailable]);

    React.useEffect(() => {
        if (bnbTokenInfo.balance !== undefined) {
            const _amountSingle = calcSingleInGivenPoolOut(
                new BigNumber(bnbTokenInfo.balance),
                new BigNumber(bnbTokenInfo.weight),
                new BigNumber(poolInfo.totalSupply),
                new BigNumber(poolInfo.totalWeight),
                new BigNumber(inputValue),
                new BigNumber(poolInfo.swapFee)
            );
            setPotentialBnbSwappedAmount(_amountSingle);
        }
    }, [
        bnbTokenInfo.balance,
        bnbTokenInfo.weight,
        inputValue,
        poolInfo.swapFee,
        poolInfo.totalSupply,
        poolInfo.totalWeight
    ]);

    const handleChange = useCallback(
        value => {
            setInputError();
            if (value !== undefined && Number(value) > 0) {
                var inputValue = library.utils.toWei(value, "ether");
                if (new BigNumber(inputValue).gt(poolInfo.totalSupply)) {
                    setInputError(
                        `Max value is ${new BigNumber(poolInfo.totalSupply)
                            .div(1e18)
                            .toFixed(4)} ${poolInfo.symbol}`
                    );
                    return;
                }
                setInputValue(inputValue);
                handleAmounts(inputValue, poolInfo);
                setIsDemandGreaterThanBalance(false)
            } else {
                setInputValue("0");
                setAmounts([]);
                setPotentialBnbSwappedAmount(new BigNumber(0));
            }
        },
        [handleAmounts, library.utils, poolInfo]
    );

    const handleSelect = useCallback(single => {
        setSelected(single);
    }, []);


    return (
        <Grid
            container
            spacing={0}
            alignItems="baseline"
            justify="center"
            className={classes.root}
        >
            <Grid item xs={6}>
                <InputToken
                    onChange={handleChange}
                    info={getCoinAvatar(poolInfo.address, chainId)}
                    size="medium"
                    error={inputError}
                />
            </Grid>
            <Grid item xs={3} textAlign="center">
                <div className={classes.buttonBox}>
                    <Button
                        variant="contained"
                        color="primary"
                        size="large"
                        onClick={selected === null ? onJoinPool : onJoinSwapPool}
                        className={classes.button}
                        disabled={isMintDisabled || selected !== null || inputError || inputValue <= 0 || isDemandGreaterThanBalance}
                    >
                        MINT
          </Button>
                </div>
            </Grid>
            <Grid item xs={3}>
                <MintWithBnbButton
                    variant="contained"
                    color="primary"
                    size="small"
                    onClick={!selected ? onSwapBnbForTokensAndMint : () => { }}
                    disabled={selected || inputError || inputValue <= 0 || parseFloat(potentialBnbSwappedAmount.div(1e18).toFixed(3)) >= parseFloat(accountBalance)}
                    bnbToSendValue={potentialBnbSwappedAmount}
                />
            </Grid>
            <Grid item xs={12}>
                <TokenList
                    poolAddress={poolInfo.address}
                    poolAmounts={amounts}
                    poolTokens={poolInfo.tokens}
                    withButton={true}
                    handleSelect={handleSelect}
                    singleAmount={singleAmount}
                    onDemandGreaterThanBalance={demandGreaterThanBalanceHandler}
                />
            </Grid>
        </Grid>
    );
};

export default MintPanel;
