import React from 'react';
import { useWeb3React } from '@web3-react/core';
import { getPool } from '../utils/contracts'
import { useToasts } from 'react-toast-notifications'
import { makeContract } from '../utils/utils';
import { abis } from '@project/contracts';

const useExitPool = (poolAddress, poolAmountIn, minAmountsOut) => {
    const { account, library, chainId } = useWeb3React()
    const { addToast } = useToasts()

    const exitPool = async (
        _poolAmountIn, 
        _minAmountsOut
    ) => {
      addToast('Waiting for transaction succes...', {
          appearance: 'info',
          autoDismiss: true,
      })
      const contract = makeContract(library, abis.indexpool, poolAddress);
      await contract.methods.exitPool(
        _poolAmountIn.toString(), 
        _minAmountsOut
      ).send({from: account}).then(()=> {
          addToast('Transaction Succes!', {
              appearance: 'success',
              autoDismiss: true,
          })
      
      }).catch((err) => {
        if(err.message.includes("User denied transaction signature")) {
          addToast('Denied Transaction', {
              appearance: 'error',
              autoDismiss: true,
          })
        } else {
          addToast('Transaction Failed', {
              appearance: 'error',
              autoDismiss: true,
          })
        }
      })
        
    }

    const handleExit = React.useCallback(
      async () => {
        await exitPool(
            poolAmountIn, 
            minAmountsOut
        )
      },
      [account, poolAmountIn, minAmountsOut],
    )
  
    return { onExitPool: handleExit }
  }
  
  export default useExitPool