import { useCallback, useEffect, useState } from 'react'
import BigNumber from 'bignumber.js'
import { useWeb3React } from '@web3-react/core';
import { addresses, abis } from "@project/contracts";
import { makeContract } from '../utils/utils';


const useCurrentTokens = (poolAddress) => {
    const { account, library, chainId } = useWeb3React()
    const [tokens, setTokens] = useState([])


    const fetchCurrentTokens = useCallback(async () => {
        const contract = makeContract(library, abis.indexpool, poolAddress);
        const _tokens = await contract.methods.getCurrentTokens().call();
        setTokens(_tokens);
    }, [library, poolAddress])

    useEffect(() => {

        if (account && library && chainId) {
            fetchCurrentTokens()
        }

    }, [account, chainId, fetchCurrentTokens, library])

    return tokens
}

export default useCurrentTokens
