import { useCallback, useEffect, useState } from 'react'
import BigNumber from 'bignumber.js'
import { useWeb3React } from '@web3-react/core';

import useBlock from './useBlock'
import useName from './useName'
import useSymbol from './useSymbol'

import useAveragePrice from './useAveragePrice';
import usePoolTokenBalance from './usePoolTokenBalance';
import usePoolTokenWeight from './usePoolTokenWeight';

import { getTokenAvatar } from '../utils/utils';

const usePoolTokenInfo = (poolAddress, poolToken) => {
    const { account, library, chainId } = useWeb3React()
    const [info, setInfo] = useState({});
    const block = useBlock();

    const tokenName = useName(poolToken);
    const tokenSymbol = useSymbol(poolToken);
    const tokenBalance = usePoolTokenBalance(poolAddress, poolToken);
    const tokenWeight = usePoolTokenWeight(poolAddress, poolToken);
    const averagePrice = useAveragePrice(poolToken);

    const fetchInfo = useCallback(async (_poolToken, _name, _symbol, _tokenBalance, _tokenWeight, _averagePrice) => {
        const avatar = getTokenAvatar(_poolToken, chainId);
        const _info = {
            address: _poolToken,
            name: _name,
            symbol: _symbol,
            balance: _tokenBalance,
            weight: _tokenWeight,
            averagePrice: _averagePrice,
            img: avatar.img
        };
        setInfo(_info);
    }, [chainId])

    useEffect(() => {
        if (account && library && poolToken && tokenName && tokenSymbol && tokenBalance && tokenWeight && averagePrice) {
            fetchInfo(poolToken, tokenName, tokenSymbol, tokenBalance, tokenWeight, averagePrice)
        }

    }, [account, poolAddress, poolToken, tokenBalance, tokenWeight, averagePrice, block, library, tokenName, tokenSymbol, fetchInfo]);

    return info
}

export default usePoolTokenInfo;
