import React, { useMemo } from 'react';
import { useWeb3React } from '@web3-react/core';
import { abis, addresses } from "@project/contracts";
import { useToasts } from 'react-toast-notifications'
import { makeContract } from '../utils/utils';

const ADDRESS_ZERO = '0x0000000000000000000000000000000000000000'

const useSwapBnbForAllTokensMint = (indexPoolAddress, indexPoolTokensCount, poolAmountOut, valueBNBIn) => {
  const { account, library, chainId } = useWeb3React()
  const { addToast } = useToasts()

  const intermediaries = useMemo(() => Array.from({ length: indexPoolTokensCount }, () => ADDRESS_ZERO), [indexPoolTokensCount])
  const handleSwap = React.useCallback(
    async () => {
      const joinswap = async () => {
        addToast('Waiting for transaction succes...', {
          appearance: 'info',
          autoDismiss: true,
        })
        const contract = makeContract(library, abis.uniswapRouterMinter, addresses[chainId].uniswapRouterMinter);
        await contract.methods.swapETHForAllTokensAndMintExact(
          indexPoolAddress,
          intermediaries,
          poolAmountOut
        ).send({ from: account, value: valueBNBIn.toString() }).then(() => {
          addToast('Transaction Succes!', {
            appearance: 'success',
            autoDismiss: true,
          })

        }).catch((err) => {
          if (err.message.includes("User denied transaction signature")) {
            addToast('Denied Transaction', {
              appearance: 'error',
              autoDismiss: true,
            })
          } else {
            addToast('Transaction Failed', {
              appearance: 'error',
              autoDismiss: true,
            })
          }
        })

      }

      await joinswap()
    },
    [account, addToast, chainId, indexPoolAddress, intermediaries, library, poolAmountOut, valueBNBIn],
  )

  return { onSwapBnbForTokensAndMint: handleSwap }
}

export default useSwapBnbForAllTokensMint
