import { theme } from '../../theme'
import { 
  makeStyles,
} from '@material-ui/core/styles';

export const useStyles = makeStyles(() => ({
    header: {
        width: '100vw',
        padding: '10px',
        backgroundColor: theme.palette.background.main,
        minHeight:'5vh',
        background: 'linear-gradient(90deg, rgba(0,0,0,0) 60%, rgba(219,165,23,0.542454481792717) 160%)',
        borderBottom: theme.palette.header.border
    },
    title: {
        display: 'none',
        color: theme.palette.text.main,

        fontVariantCaps: 'all-small-caps',
        '&:hover': {
            color: theme.palette.primary,
        },
        [theme.breakpoints.up('sm')]: {
            display: 'block',
        },},
    wrapper: {
        display: 'flex',
        margin: '5px',
        flexWrap: 'wrap',
        justifyContent: 'center',
    },
    button: {
        padding: '5px',
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
        minHeight: '80px',
        minWidth: '130px',
        color: theme.palette.text.primary,
        "&:hover": {
            borderBottom: `solid 6px ${theme.palette.primary.main}`
        }
    },
    nowrapper: {
        display: 'flex',
        flexWrap: 'nowrap',
        alignItems: 'center',
        justifyContent: 'center',
    },
    navwrapper:{
        display: 'flex',
        flexWrap: 'nowrap',
        alignItems: 'center',
        justifyContent: 'center',
        paddingLeft: '25%'
    },
    modal: {
        width: '100vw',
        height: '100vh',
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
        position: 'fixed'
    },
    logo: {
        height: '5vh',
    },
    headerlogo:{
        margin: '10px'
    }
   
}));