import React from 'react';
import { 
    Grid,
    Typography,
    Button, 
    ButtonGroup
} from '@material-ui/core'
import MaterialCard from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import CardActions from '@material-ui/core/CardActions';

import { formatter } from '../../../utils/utils'
import { useWeb3React } from '@web3-react/core';
import { useStyles } from './ActionCard.styles';

import useCurrentTokens from '../../../hooks/useCurrentTokens';

import MintPanel from '../../../components/panels/mintpanel/MintPanel.component';
import SwapPanel from '../../../components/panels/swappanel/SwapPanel.component';
import BurnPanel from '../../../components/panels/burnpanel/BurnPanel.component';


import useTotalWeight from '../../../hooks/useTotalWeight';
import useTotalSupply from '../../../hooks/useTotalSupply';
import usePoolTokenBalances from '../../../hooks/usePoolTokenBalances';
import usePoolTokenWeights from '../../../hooks/usePoolTokenWeights';
import useAllowance from '../../../hooks/useAllowance';
import useGetSwapFee from '../../../hooks/useGetSwapFee';
import CircularProgress from '@material-ui/core/CircularProgress';

const ActionCard = ({ poolInfo }) => {
    const { account } = useWeb3React();
    const classes = useStyles();
    const poolSupply = useTotalSupply(poolInfo.address);
    const poolTokenWeights = usePoolTokenWeights(poolInfo.address, poolInfo.tokens);
    const poolTokenBalances = usePoolTokenBalances(poolInfo.address, poolInfo.tokens);
    const poolAllowance = useAllowance(poolInfo.address, poolInfo.address);
    const [ currentActionTab, setCurrentActionTab ] = React.useState('MINT'); 
    
    const [ component, setComponent ] = React.useState(poolInfo ? <MintPanel poolInfo={poolInfo}></MintPanel> : <CircularProgress color='primary'/>)

    React.useEffect(() => {
        if(currentActionTab === 'MINT') {
            setComponent(<MintPanel poolInfo={poolInfo}/>)
        } else {
            setComponent(<BurnPanel poolInfo={poolInfo}/>)
        }
        
    }, [account, poolInfo, poolSupply, poolTokenWeights, poolTokenBalances])

    const handleClick = (e, tab='MINT') => {
        if(tab === 'MINT') {
            setComponent(
                <MintPanel poolInfo={poolInfo}/>
            )
            setCurrentActionTab(tab)
        } else {
            setComponent(
            <BurnPanel 
                poolInfo={poolInfo}
            />)
            setCurrentActionTab(tab)
        }
    }

    return (
        <MaterialCard className={classes.card}>
            <Grid 
                container
                alignItems='flex-start'
            >
                <Grid container item xs={12}>
                    <ButtonGroup className={classes.header}  >
                        <Grid item xs>
                            <Button
                                size="medium"
                                color="primary"
                                variant='outlined'
                                className={classes.button}
                                onClick={(e)=>{handleClick(e, 'MINT')}}
                            >
                                Mint
                            </Button>
                        </Grid>
                        <Grid item xs>
                            <Button
                                size="medium"
                                color="primary"
                                variant='outlined'
                                className={classes.button}
                                onClick={(e)=>{handleClick(e, 'BURN')}}
                            >
                                Burn
                            </Button>
                        </Grid>
                        

                    </ButtonGroup>
                </Grid>
                
                <Grid item  xs={12} className={classes.content} >
                    {component}
                </Grid>
                
            </Grid>
        </MaterialCard>
    )
}

export default ActionCard;

