import { useCallback, useEffect, useState } from 'react'
import BigNumber from 'bignumber.js'
import { useWeb3React } from '@web3-react/core';
import { addresses, abis } from "@project/contracts";
import { makeContract } from '../utils/utils';

const useSymbol = (tokenAddress) => {
    const { account, library, chainId } = useWeb3React()
    const [symbol, setSymbol] = useState('')

    const fetchSymbol = useCallback(async () => {
        const contract = makeContract(library, abis.erc20, tokenAddress);
        const _symbol = await contract.methods.symbol().call();
        setSymbol(_symbol.toString())
    }, [library, tokenAddress])

    useEffect(() => {

        if (account && library && tokenAddress) {
            fetchSymbol()
        }
    }, [account, library, chainId, tokenAddress, fetchSymbol])

    return symbol;
}

export default useSymbol;
