import { useCallback, useEffect, useState } from 'react'
import BigNumber from 'bignumber.js'
import { useWeb3React } from '@web3-react/core';
import { addresses, abis } from "@project/contracts";
import { makeContract } from '../utils/utils';

const useTotalWeight = (poolAddress) => {
    const { account, library, chainId } = useWeb3React()
    const [totalWeight, setTotalWeight] = useState('0')

    const fetchTotalWeight = useCallback(async (_poolAddress) => {
        const contract = makeContract(library, abis.indexpool, _poolAddress);
        const totWeight = await contract.methods.getTotalDenormalizedWeight().call();
        //console.log('totWeight',totWeight)
        setTotalWeight(totWeight);
    }, [library])

    useEffect(() => {

        if (account && library && chainId && poolAddress) {
            fetchTotalWeight(poolAddress)
        }

    }, [account, chainId, fetchTotalWeight, library, poolAddress])

    return totalWeight
}

export default useTotalWeight
