import React, { useEffect } from "react";
import { makeStyles } from "@material-ui/core/styles";
import { Grid, Typography, Button } from "@material-ui/core";
import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import ListItemIcon from "@material-ui/core/ListItemIcon";
import ListItemAvatar from "@material-ui/core/ListItemAvatar";

import Avatar from "@material-ui/core/Avatar";


import ApproveButton from "../../buttons/ApproveButton.component";

import { useWeb3React } from "@web3-react/core";

import InputToken from "../../inputs/InputToken";
import { getCoinAvatar } from "../../../utils/utils";

const useStyles = makeStyles(theme => ({
    root: {
        width: "100%",
        maxHeight: "45vh",
        overflowY: "scroll"
    }
}));

const TokenList = ({
    poolAddress,
    poolTokens,
    poolAmounts,
    withButton,
    handleSelect,
    singleAmount,
    onDemandGreaterThanBalance
}) => {
    const { account, chainId, library } = useWeb3React();
    const classes = useStyles();
    const [single, setSingle] = React.useState();
    const [approvedMap, setApprovedMap] = React.useState({});

    const [token, setToken] = React.useState();

    useEffect(() => {
        const isSingle = false
        handleSelect(null);
        setSingle(isSingle)
    }, [handleSelect]);

    // const handleToggle = (value) => () => {
    //     const isSingle = false
    //     setChecked([value]);
    //     setToken(value);
    //     if (isSingle) {
    //         handleSelect(value);
    //         handleMintDisabling(!approvedMap[value])
    //     } else {
    //         const isAllApproved = !poolTokens.find((token) => !approvedMap[token])
    //         handleSelect(null);
    //         handleMintDisabling(!isAllApproved)
    //     }
    //     setSingle(isSingle)
    // };

    const handleApproved = React.useCallback(
        (tokenAddress, isApproved) => {
            setApprovedMap(prevApprovedMap => ({
                ...prevApprovedMap,
                [tokenAddress]: isApproved
            }));
            // eslint-disable-next-line react-hooks/exhaustive-deps
        },
        []
    );

    return (
        <>
            {poolTokens && poolAmounts && poolAddress ? (
                <Grid container>
                    <List className={classes.root} dense={true}>
                        {poolTokens.map(value => {
                            const address = poolAddress;
                            const id = poolTokens.indexOf(value);
                            return (
                                <ListItem key={value} role={undefined} className={classes.item}>
                                    <Grid
                                        container
                                        alignItems="center"
                                        justify="center"
                                        className={classes.nowrapper}
                                    >
                                        <Grid item>
                                            <ListItemIcon>
                                                {/*<Checkbox*/}
                                                {/*  edge="start"*/}
                                                {/*  checked={*/}
                                                {/*    single === undefined*/}
                                                {/*      ? false*/}
                                                {/*      : single === true*/}
                                                {/*      ? checked.indexOf(value) !== -1*/}
                                                {/*      : true*/}
                                                {/*  }*/}
                                                {/*  tabIndex={-1}*/}
                                                {/*  color="primary"*/}
                                                {/*  onClick={handleToggle(value)}*/}
                                                {/*/>*/}
                                            </ListItemIcon>
                                        </Grid>
                                        <Grid item>
                                            <ListItemAvatar>
                                                <Avatar
                                                    alt={`Avatar`}
                                                    src={getCoinAvatar(value, chainId).img}
                                                />
                                            </ListItemAvatar>
                                        </Grid>
                                        <Grid item xs>
                                            <InputToken
                                                info={getCoinAvatar(value, chainId)}
                                                placeholder={
                                                    singleAmount !== undefined && token == value
                                                        ? singleAmount
                                                        : poolAmounts[id]
                                                }
                                                onDemandGreaterThanBalance={onDemandGreaterThanBalance}
                                            />
                                        </Grid>
                                        <Grid item xs={3}>
                                            {withButton == true ? (
                                                <ListItem>
                                                    <ApproveButton
                                                        poolAddress={address}
                                                        poolTokenAddress={value}
                                                        handleApproved={handleApproved}
                                                    />
                                                </ListItem>
                                            ) : (
                                                <></>
                                            )}
                                        </Grid>
                                    </Grid>
                                </ListItem>
                            );
                        })}
                    </List>
                </Grid>
            ) : (
                <></>
            )}
        </>
    );
};

TokenList.defaultProps = {
    handleMintDisabling: () => { },
    onDemandGreaterThanBalance: () => { }
};

export default TokenList;
