import { useCallback, useEffect, useState } from 'react'
import BigNumber from 'bignumber.js'
import { useWeb3React } from '@web3-react/core';
import { addresses, abis } from "@project/contracts";
import { makeContract } from '../utils/utils';
import { getPool } from '../utils/contracts';
import useBlock from './useBlock'

const useGetSwapFee = (poolAddress) => {
    const { account, library, chainId } = useWeb3React()
    const [swapFee, setSwapFee] = useState('0')
    const block = useBlock()

    const getSwapFee = useCallback(async (_poolAddress) => {
        const contract = makeContract(library, abis.indexpool, _poolAddress);
        const fee = await contract.methods.getSwapFee().call();
        setSwapFee(fee);
    }, [library])

    useEffect(() => {

        if (account && library && chainId) {
            getSwapFee(poolAddress)
        }

    }, [account, library, block, chainId, getSwapFee, poolAddress])

    return swapFee
}

export default useGetSwapFee
