import React from "react";
import {
  Grid,
  Typography,
  Button
} from '@material-ui/core'
import { useStyles } from './Dashboard.styles'
import { useWeb3React } from "@web3-react/core";
import { addresses, abis } from "@project/contracts";

import PoolCard from "../../components/cards/poolcard/PoolCard.component";
import InfoCard from '../../components/cards/infocard/InfoCard.component'
import ActionCard from '../../components/cards/actioncard/ActionCard.component'

function Dashboard() {
  const { account, chainId, library } = useWeb3React();
  const classes = useStyles();

  return (
    <Grid
      container
      spacing={4}
      alignItems='flex-start'
      justify='center'
      className={classes.container}
    >
      {chainId === 56 ?
        <>
          <Grid item >
            <PoolCard
              poolAddress={addresses[chainId].biT10}
            />
          </Grid>
        </>
        :
        <>
          <div>Please Connect Wallet</div>
        </>
      }

    </Grid>
  );
}

export default Dashboard;

