import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { Button } from '@material-ui/core';

const useStyles = makeStyles({
    underText: {
        color: '#747474',
        margin: 0,
        fontSize: '0.75rem',
        marginTop: '3px',
        textAlign: 'left',
        fontFamily: '"Roboto", "Helvetica", "Arial", sans - serif',
        fontWeight: 400,
        lineHeight: 1.66,
        letterSpacing: '0.03333em',
    },
    button: {
        height: '5.2vh',
        marginTop: '3vh'
    }

});

export const MintWithBnbButton = ({ onClick, disabled, bnbToSendValue }) => {

    const styles = useStyles()
    return (
        <div>
            <Button
                variant='contained'
                color='primary'
                size='small'
                onClick={onClick}
                className={styles.button}
                disabled={disabled}
            >
                MINT WITH BNB
        </Button>
            <p className={styles.underText}>Send: {bnbToSendValue.div(1e18).toFixed(3)} BNB</p>
        </div>
    )
};


export const BurnWithBnbButton = ({ onClick, disabled, bnbToSendValue }) => {

    const styles = useStyles()
    return (
            <div>
                <Button
                        variant='contained'
                        color='primary'
                        size='small'
                        onClick={onClick}
                        className={styles.button}
                        disabled={disabled}
                >
                    BURN TO BNB
                </Button>
                <p className={styles.underText}>Send: {bnbToSendValue.div(1e18).toFixed(3)} BNB</p>
            </div>
    )
};
