import { useCallback, useEffect, useState } from 'react'
import BigNumber from 'bignumber.js'
import { useWeb3React } from '@web3-react/core';
import { getPool } from '../utils/contracts';
import { makeContract } from '../utils/utils';
import { abis } from '@project/contracts';

const usePoolTokenWeights = (poolAddress, poolTokens) => {
    const { account, library, chainId } = useWeb3React()
    const [weights, setTokenWeights] = useState(['0'])

    const fetchWeights = useCallback(async (tokenArr) => {
        const contract = makeContract(library, abis.indexpool, poolAddress);
        const weights = [];
        for (let i = 0; i < tokenArr.length; i++) {
            const tokenWeight = await contract.methods.getDenormalizedWeight(tokenArr[i]).call();
            weights.push(tokenWeight);

        }
        setTokenWeights(weights);
    }, [library, poolAddress])

    useEffect(() => {

        if (account && library && poolTokens && poolAddress) {
            fetchWeights(poolTokens)
        }

    }, [account, library, poolTokens, chainId, poolAddress, fetchWeights])

    return weights
}

export default usePoolTokenWeights
