import React from 'react';
import { useWeb3React } from '@web3-react/core';
import { addresses, abis } from "@project/contracts";
import { useToasts } from 'react-toast-notifications'
import { makeContract } from '../utils/utils';

const useApprove = (spender, amount, contractAddress) => {
    const { account, library, chainId } = useWeb3React()
    const { addToast } = useToasts()

    const approve = async (_spender, _amount, _contractAddress) => {
        const contract = makeContract(library, abis.erc20, _contractAddress);   
        await contract.methods.approve(
            _spender, 
            _amount
        ).send({from: account}).then(()=> {
            addToast('Transaction Succes!', {
                appearance: 'success',
                autoDismiss: true,
            })
        
        }).catch((err) => {
          if(err.message.includes("User denied transaction signature")) {
            addToast('Denied Transaction', {
                appearance: 'error',
                autoDismiss: true,
            })
          } else {
            addToast('Transaction Failed', {
                appearance: 'error',
                autoDismiss: true,
            })
          }
        })
    }

    const handleApprove = React.useCallback(
      async () => {
        await approve(
            spender, amount.toString(), contractAddress
        )
      },
      [account, spender, amount, contractAddress],
    )
  
    return { onApprove: handleApprove }
  }
  
  export default useApprove