import React from 'react';
import {
    Grid,
} from '@material-ui/core'

import { Link } from 'react-router-dom'
import MaterialCard from '@material-ui/core/Card';

import Avatar from '@material-ui/core/Avatar';
import AvatarGroup from '@material-ui/lab/AvatarGroup';

import { useWeb3React } from '@web3-react/core';
import { useStyles } from './PoolCard.styles';

import { getCoinAvatar } from '../../../utils/utils';

import useCurrentTokens from '../../../hooks/useCurrentTokens';

import TokenAvatar from '../../avatars/TokenAvatar';

const PoolCard = ({ poolAddress }) => {
    const { chainId } = useWeb3React();

    const poolAvatar = getCoinAvatar(poolAddress, chainId);
    const currentTokens = useCurrentTokens(poolAddress);

    const [content, setContent] = React.useState([]);

    const classes = useStyles();

    React.useEffect(() => {
        const renderContent = () => {
            const _content = [];
            for (let i = 0; i < currentTokens.length; i++) {
                _content.push(
                    <TokenAvatar
                        tokenAddress={currentTokens[i]}
                    />
                );
            }
            setContent(_content);
        };

        if (currentTokens) {
            renderContent(currentTokens)
        }

    }, [currentTokens])

    return (
        <MaterialCard
            className={classes.card}
            component={Link}
            to={`/funds/${poolAddress}`}
        >
            <Grid
                container
                spacing={1}
                direction="column"
                justifyContent="center"
                alignItems="center"
            >
                <Grid item xs className={classes.header}>
                    <Avatar src={poolAvatar.img} className={classes.avatar} />
                </Grid>
                <Grid item xs={12} >
                    <AvatarGroup max={5} className={classes.content}>
                        {content}
                    </AvatarGroup>
                </Grid>
            </Grid>
        </MaterialCard>

    )
}

export default PoolCard;