import React, { useEffect } from "react";
import { Grid, TextField, Typography, Button } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import InputAdornment from "@material-ui/core/InputAdornment";
import { useWeb3React } from "@web3-react/core";
import { formatter } from "../../utils/utils";
import useTokenBalance from "../../hooks/useTokenBalance";

const useStyles = makeStyles(theme => ({
  button: {
    maxHeight: 26,
    padding: 0
  },
  root: {
    display: "flex",
    flexWrap: "wrap",
    "& .MuiTextField-root": {
      margin: theme.spacing(1),
      width: 200
    }
  },
  margin: {
    margin: theme.spacing(0.5)
  },
  withoutLabel: {
    marginTop: theme.spacing(4)
  },
  textField: {
    width: "25ch"
  },
  input: {
    fontSize: theme.typography.subtitle1.fontSize
  },
  helperText: {
    color: theme.palette.text.dark
  },
  error: {
    "& .MuiOutlinedInput-root .MuiOutlinedInput-notchedOutline": {
      borderColor: "red"
    },
    "& .MuiOutlinedInput-input": {
      color: "red"
    },
    "&:hover .MuiOutlinedInput-input": {
      color: "red"
    }
  }
}));

const InputToken = ({ onDemandGreaterThanBalance, ...props }) => {
  const { account, library } = useWeb3React();
  const [info, setInfo] = React.useState("");
  const balance = useTokenBalance(info ? info.address : "");
  const classes = useStyles();
  const [value, setValue] = React.useState("");
  React.useEffect(() => {
    setInfo(props.info);
  }, [props.info, account, balance]);

  useEffect(() => {
    const bnDemandValue = Number(props.placeholder);
    if (onDemandGreaterThanBalance && bnDemandValue > 0) {
      const newValue = bnDemandValue > Number(balance);
      onDemandGreaterThanBalance(props.info.symbol, newValue);
    }
  }, [balance, onDemandGreaterThanBalance, props.info.symbol, props.placeholder])

  return (
    <Grid
      container
      alignItems="center"
      justify="center"
      className={classes.container}
    >

      <Grid item xs={12} className={classes.margin}>
        <TextField
          variant="outlined"
          value={value}
          size={props.size ? props.size : "small"}
          onChange={e => {
            e.preventDefault();
            setValue(e.target.value)
            if (props.onChange) props.onChange(e.target.value);
          }}
          placeholder={
            library && props && props.placeholder && !isNaN(props.placeholder)
              ? formatter.format(
                library.utils.fromWei(props.placeholder, "ether")
              )
              : "0"
          }
          FormHelperTextProps={{
            className: classes.helperText
          }}
          fullWidth={true}
          InputProps={{
            classes: {
              input: classes.input
            },
            startAdornment: (
              <InputAdornment position="start">
                {props.maxButton && (
                  <Button
                    variant="contained"
                    color="primary"
                    size="small"
                    className={classes.button}
                    onClick={() => {
                      setValue(formatter.format(
                        library.utils.fromWei(balance, "ether")
                      ))
                      props.onChange(
                        formatter.format(
                          library.utils.fromWei(balance, "ether")
                        )
                      );
                    }}
                  >
                    MAX
                  </Button>
                )}
              </InputAdornment>
            ),
            endAdornment: (
              <InputAdornment position="start">
                <Typography variant="caption" color="primary">
                  {info ? info.symbol : ""}
                </Typography>
              </InputAdornment>
            )
          }}
          helperText={`Balance: ${balance ? formatter.format(library.utils.fromWei(balance, 'ether')) : '0'}`}
          error={props.error}
          autoComplete='off'
          className={props.toBig === true ? classes.error : ''}
        />
      </Grid>
    </Grid>
  );
};

InputToken.defaultProps = {
  onInputGreaterThanBalance: () => { }
}

export default React.memo(InputToken, (prevProps, nextProps) => {
  return prevProps.info.address === nextProps.info.address
    && prevProps.placeholder === nextProps.placeholder
    && prevProps.error === nextProps.error
});
