import React from 'react';
import { 
    Grid,
    Button,
    TextField,
    Menu,
    MenuItem,
    Avatar,
    Typography
} from '@material-ui/core'

import {
    withStyles,
    makeStyles,
  } from '@material-ui/core/styles';
import ArrowDropDownIcon from '@material-ui/icons/ArrowDropDown';
import useTokenBalance from '../../hooks/useTokenBalance';
import InputToken from './InputToken';
import { useWeb3React } from '@web3-react/core';
import { addresses, abis } from "@project/contracts";
import { formatter, getCoinAvatar } from '../../utils/utils'
import { theme } from '../../theme';

const CssTextField = withStyles({
    root: {
        width: '100%',
        '& .MuiInputBase-root': {
            color: 'inherit',
            fontSize: theme.typography.body1.fontSize
          },
        
        '& .MuiOutlinedInput-root': {
            '& fieldset': {
            border: 'none'
            },
        },
    },
})(TextField);

const useStyles = makeStyles({
    textfield: {
        color: theme.palette.text.primary,
    },
    errorfield: {
        color: 'red',
        
    },
    container: {
        flexWrap: 'nowrap',
        minWidth:'420px'
    },
    label: {
        display: 'flex',
        direction: 'row',
       
    },
    menu:{
        '& .MuiPaper-root': {
            backgroundColor: theme.palette.paper.main
        },
    }
});


const InputPicker = (props) => {
    const {account, chainId, library } = useWeb3React();
    const [ anchorEl, setAnchorEl ] = React.useState(null);
    const [ content, setContent] = React.useState([]);

    const balance = useTokenBalance(props.selected ? props.selected.address : '')

    const classes = useStyles();

    const handleSelect = (item) => {
        setAnchorEl(null);
        props.handleSelected(getCoinAvatar(item, chainId));
    }

    const renderContent = (items) => {
        const _content = [];
        
        for(let i = 0; i < items.length; i++){
            const tokenAvatar = getCoinAvatar(items[i], chainId);
            _content.push(
                <MenuItem key={i} onClick={(e)=>{handleSelect(items[i])}} >
                    <Avatar  alt="payment" src={tokenAvatar.img}/>
                </MenuItem>
        );
        setContent(_content);
        }
    }


    React.useEffect(() => {

       renderContent(props.tokens);
    
    }, [props, balance]);

    return (
        <>
            <Grid 
                container 
                alignItems='center'
                justify='center'
                className={classes.container}
            >
                <Grid container direction='row' item xs={10} alignItems="center" justify="center" className={props.toBig ? classes.errorfield : classes.textfield}>
                    <InputToken 
                        onChange={props.onChange}
                        info={getCoinAvatar(props.poolAddress, chainId)} 
                        size='medium'
                        toBig={props.toBig}
                    />
                </Grid>
                <Grid item xs>
                    <Button onClick={(e)=>{setAnchorEl(e.currentTarget);}} size='medium' style={{minHeight:'80px', alignItems:'flex-start'}}>
                        <Avatar src={props.selected ? props.selected.img : ''} alt="payment" />
                        <ArrowDropDownIcon size='small'/>
                    </Button>
                    <Menu
                        id="simple-menu"
                        anchorEl={anchorEl}
                        open={Boolean(anchorEl)}
                        onClose={(e)=>{setAnchorEl(null)}}
                        className={classes.menu}
                    >
                        {content}
                    </Menu>
                </Grid>
            </Grid>
            <Grid container item xs={12} alignItems='center' justify='flex-end' >
                <Grid item xs={6}>
                    <Typography variant='caption' color='textPrimary'>
                        {props.toBig ? 'Not enough tokens in pool to swap' : ''}
                    </Typography>
                </Grid>            
            </Grid>
        </>
       
    )
}

export default InputPicker; 