import { addresses, abis } from "@project/contracts";
import { makeContract } from './utils';

export const getPool = (library, chainId) => {
    return makeContract(library, abis.indexpool, addresses[chainId].biT10);
}

export const getOracle = (library, chainId) => {
    return makeContract(library, abis.uniswapV2oracle, addresses[chainId].uniswapV2oracle);
}

export const getSeller = (library, chainId) => {
    return makeContract(library, abis.indexseller, addresses[chainId].indexseller);
}