import React, { useCallback, useMemo, useState } from 'react';
import {
    Grid,
    Typography,
    Link
} from '@material-ui/core'
import BigNumber from 'bignumber.js'
import MaterialCard from '@material-ui/core/Card';
import CardHeader from '@material-ui/core/CardHeader';
import Avatar from '@material-ui/core/Avatar';

import { useWeb3React } from '@web3-react/core';

import { useStyles } from './InfoCard.styles';
import { formatter } from '../../../utils/utils'
import TokenInfo from '../../tokeninfo/TokenInfo.component'

const PoolContent = ({ poolInfo, handleSetUsdValue }) => {

    if (!poolInfo) {
        return null;
    }

    return (
        <>
            {
                poolInfo.tokens.map((token) => (
                    <Grid container item lg={6} key={token.address} justify='center' alignItems='center'>
                        <TokenInfo
                            poolInfo={poolInfo}
                            poolToken={token}
                            handleSetUsdValue={handleSetUsdValue}
                        />
                    </Grid>)
                )
            }
        </>
    )
}

const InfoCard = ({ poolInfo }) => {
    const { chainId, library } = useWeb3React();
    const classes = useStyles();
    const [usdValueTokensMap, setUsdValueTokensMap] = useState({});
    const [tvl, setTvl] = useState(new BigNumber(0));

    const handleSetUsdValue = useCallback((token, valueBN) => {
        setUsdValueTokensMap((prevUsdValueTokensMap) => ({ ...prevUsdValueTokensMap, [token]: valueBN }));
        const valuesArray = Object.values(usdValueTokensMap);
        if (valuesArray.length) {
            setTvl(new BigNumber.sum(...valuesArray))
        }
    }, [usdValueTokensMap]);

    return (
        <MaterialCard className={classes.card}>
            <Grid
                container
                spacing={0}
            >
                <CardHeader
                    avatar={
                        <Avatar className={classes.avatar} >
                            <img src={poolInfo ? poolInfo.img : ''} className={classes.avatarImage} />
                        </Avatar>
                    }
                    subheader={
                        <Grid
                            container
                            alignItems='flex-start'
                        >
                            <Grid container item lg={3} >
                                <Grid item>
                                    <Typography variant='h6' color='textPrimary'>
                                        {poolInfo ? poolInfo.name : ''}
                                    </Typography>
                                </Grid>
                            </Grid>
                            {/* <Grid container item lg={3} spacing={1} alignItems='center' >
                                <Grid item>
                                    <Typography variant='h6' color='textPrimary'>
                                        $ 0.00
                                    </Typography>
                                </Grid>
                                    <Typography variant='h6' color='primary'>
                                        (
                                    </Typography>
                                <Grid item>
                                    <Typography variant='h6' color='error'>
                                        + 0.00 %
                                    </Typography>
                                </Grid>
                                    <Typography variant='h6' color='primary'>
                                        )
                                    </Typography>     
                            </Grid> */}
                            <Grid container item className={classes.actions} alignItems='flex-start' direction='row'>
                                <Grid container item xs direction='column' spacing={0} >
                                    <Grid item >
                                        <Typography variant='caption' color='primary'>
                                            ADDRESS:
                                        </Typography>
                                    </Grid>
                                    <Grid item>
                                        <Typography variant='caption' color='primary'>
                                            SUPPLY:
                                        </Typography>
                                    </Grid>
                                    {/* <Grid item>
                                        <Typography variant='caption' color='primary'>
                                            VOLUME: 
                                        </Typography>
                                    </Grid> */}
                                    <Grid item>
                                        <Typography variant='caption' color='primary'>
                                            TVL:
                                        </Typography>
                                    </Grid>
                                </Grid>

                                <Grid container item xs direction='column' spacing={0} >
                                    <Grid item >
                                        <Typography
                                            variant='caption'
                                            color='primary'
                                            component={Link}
                                            style={{ textDecoration: 'none' }}
                                            href={poolInfo && chainId == 97 ? `https://testnet.bscscan.com/address/${poolInfo.address}` : `https://bscscan.com/address/${poolInfo.address}`}
                                            target="_blank"
                                        >
                                            {poolInfo ? poolInfo.address : ''}
                                        </Typography>
                                    </Grid>
                                    <Grid item>
                                        <Typography variant='caption' color='textPrimary'>
                                            {poolInfo ? library.utils.fromWei(poolInfo.totalSupply.toString(), 'ether') : ''}
                                        </Typography>
                                    </Grid>
                                    {/* <Grid item>
                                        <Typography variant='caption' color='textPrimary'>
                                            *volume* 
                                        </Typography>
                                    </Grid> */}
                                    <Grid item>
                                        <Typography variant='caption' color='textPrimary'>
                                            ${formatter.format(tvl.toNumber())}
                                        </Typography>
                                    </Grid>
                                </Grid>
                            </Grid>
                        </Grid>
                    }
                    className={classes.header}
                />
                <Grid
                    container
                    item
                    xs={12}
                    spacing={4}
                    alignItems='center'
                    justify='center'
                    className={classes.content}
                >
                    <PoolContent poolInfo={poolInfo} handleSetUsdValue={handleSetUsdValue} />
                </Grid>
            </Grid>
        </MaterialCard>
    )
}

export default InfoCard;

