import { useCallback, useEffect, useState } from 'react'
import BigNumber from 'bignumber.js'
import { useWeb3React } from '@web3-react/core';
import useBlock from './useBlock'

import useName from './useName';
import useSymbol from './useSymbol';
import usePoolTokenBalances from './usePoolTokenBalances';
import usePoolTokenUsedBalance from './usePoolTokenUsedBalance';
import usePoolTokenWeights from './usePoolTokenWeights';
import useCurrentTokens from './useCurrentTokens';
import useTotalWeight from './useTotalWeight';
import useTotalSupply from './useTotalSupply';
import useAveragePrice from './useAveragePrice';
import useGetSwapFee from './useGetSwapFee';
import { getTokenAvatar } from '../utils/utils';

const usePoolInfo = (poolAddress) => {
    const { account, library, chainId } = useWeb3React()
    const [info, setInfo] = useState();
    const block = useBlock();
    const name = useName(poolAddress);
    const symbol = useSymbol(poolAddress);
    const poolTokens = useCurrentTokens(poolAddress);
    const totalWeight = useTotalWeight(poolAddress);
    const totalSupply = useTotalSupply(poolAddress);
    const swapFee = useGetSwapFee(poolAddress);
    const poolTokenBalances = usePoolTokenBalances(poolAddress, poolTokens);
    const poolTokenWeights = usePoolTokenWeights(poolAddress, poolTokens);

    const fetchInfo = useCallback(async (_name, _symbol, _poolTokens, _poolTokenBalances, _poolTokenWeights, _totalWeight, _totalSupply, _swapFee) => {
        const avatar = getTokenAvatar(poolAddress, chainId);
        const _info = {
            address: poolAddress,
            name: _name,
            img: avatar.img,
            symbol: _symbol,
            tokens: _poolTokens,
            balances: _poolTokenBalances,
            weights: _poolTokenWeights,
            totalSupply: _totalSupply,
            totalWeight: _totalWeight,
            swapFee: _swapFee,

        }
        setInfo(_info);
    }, [poolAddress, chainId])

    useEffect(() => {
        if (account && library && name && symbol && poolTokens && totalWeight && totalSupply && poolTokenBalances && poolTokenWeights) {
            fetchInfo(name, symbol, poolTokens, poolTokenBalances, poolTokenWeights, totalWeight, totalSupply, swapFee)
        }

    }, [poolAddress, account, name, symbol, poolTokens, totalWeight, totalSupply, block, poolTokenBalances, poolTokenWeights, library, fetchInfo, swapFee]);

    return info
}

export default usePoolInfo;
