import { theme } from '../../../theme'
import { 
  makeStyles,
} from '@material-ui/core/styles';

export const useStyles = makeStyles({
    card: {
        height: '100%',
        backgroundColor: theme.palette.paper.main,
        color: theme.palette.text.primary,
        borderRadius: theme.palette.border.radius,
        border: theme.palette.border.color,
    },
    header: {
        // height: '15vh',
        width: '100%',
        backgroundColor: theme.palette.background.main,
        background: 'linear-gradient(90deg, rgba(0,0,0,0) 60%, rgba(219,165,23,0.542454481792717) 160%)',
        borderBottom: theme.palette.border.color,
    },
    button: {
        padding: '5px',
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
        minHeight: '60px',
        minWidth: '130px',
        color: theme.palette.text.primary,
        "&:hover": {
            borderBottom: `solid 6px ${theme.palette.primary.main}`
        }
    },
    content: {
        padding: '25px',
        color: theme.palette.text.main 
    },
    nowrapper: {
        display: 'flex',
        flexWrap: 'nowrap'
    },
    avatar: {
        minWidth: '70px',
        minHeight: '70px',
        backgroundColor: theme.palette.text.primary
    },
    avatarImage: {
        maxWidth: '70px'
    },
    actions: {
        minWidth: '250px'
    }
});