import React, { useMemo } from "react";
import { useWeb3React } from "@web3-react/core";
import { abis, addresses } from "@project/contracts";
import { useToasts } from "react-toast-notifications";
import { makeContract } from "../utils/utils";

const ADDRESS_ZERO = "0x0000000000000000000000000000000000000000";

const useSwapBnbForAllTokensBurn = (
  indexPoolAddress,
  minAmountsOut,
  indexPoolTokensCount,
  poolAmountIn,
  valueBNBIn
) => {
  const { account, library, chainId } = useWeb3React();
  const { addToast } = useToasts();
  const intermediaries = useMemo(
    () => Array.from({ length: indexPoolTokensCount }, () => ADDRESS_ZERO),
    [indexPoolTokensCount]
  );
  const handleSwap = React.useCallback(async () => {
    const joinswap = async () => {
      addToast("Waiting for transaction success...", {
        appearance: "info",
        autoDismiss: true
      });
      const contract = makeContract(
        library,
        abis.uniswapRouterBurner,
        addresses[chainId].uniswapRouterBurner
      );

      await contract.methods
        .burnForAllTokensAndSwapForETH(
          indexPoolAddress,
          minAmountsOut,
          intermediaries,
          poolAmountIn,
          valueBNBIn.toString()
        )
        .send({ from: account, value: valueBNBIn.toString() })
        .then(() => {
          addToast("Transaction Success!", {
            appearance: "success",
            autoDismiss: true
          });
        })
        .catch(err => {
          if (err.message.includes("User denied transaction signature")) {
            addToast("Denied Transaction", {
              appearance: "error",
              autoDismiss: true
            });
          } else {
            addToast("Transaction Failed", {
              appearance: "error",
              autoDismiss: true
            });
          }
        });
    };

    await joinswap();
  }, [
    account,
    addToast,
    chainId,
    indexPoolAddress,
    intermediaries,
    library,
    indexPoolAddress,
    minAmountsOut,
    indexPoolTokensCount,
    poolAmountIn,
    valueBNBIn
  ]);

  return { onSwapBnbForTokensAndBurn: handleSwap };
};

export default useSwapBnbForAllTokensBurn;
