import { useCallback, useEffect, useState } from 'react'
import BigNumber from 'bignumber.js'
import { useWeb3React } from '@web3-react/core';
import { addresses, abis } from "@project/contracts";
import { makeContract } from '../utils/utils';

const useName = (tokenAddress) => {
    const { account, library, chainId } = useWeb3React()
    const [name, setName] = useState('')

    const fetchName = useCallback(async () => {
        const contract = makeContract(library, abis.erc20, tokenAddress);
        const _name = await contract.methods.name().call();
        setName(_name.toString())
    }, [library, tokenAddress])

    useEffect(() => {

        if (account && library && tokenAddress) {
            fetchName()
        }
    }, [account, library, chainId, tokenAddress, fetchName])

    return name
}

export default useName
